import React from 'react'
import './technologies.sass'

const technologies = () => (
  <section id="technologies" className="flex">
    <div className="tech-wrap">
      <header>
        <strong>Technologies</strong>{' '}
      </header>
      <div className="has-padding-vertical-10">
        Technology stack I often work with includes
      </div>
      <div className="stack-wrapper">
        <div className="stack button-primary">HTML5</div>
        <div className="stack button-primary">JavaScript</div>
        <div className="stack button-primary">CSS3</div>
        <div className="stack button-primary">SASS</div>
        <div className="stack button-primary">Gulp</div>
        <div className="stack button-primary">React</div>
        <div className="stack button-primary">NodeJS</div>
        <div className="stack button-primary">VueJS</div>
        <div className="stack button-primary">Gatsby</div>
        <div className="stack button-primary">And more...</div>
      </div>
    </div>
  </section>
)

export default technologies
