import React from 'react'
import './form.sass'

const form = () => (
  <section id="contact" className="flex">
    <div className="contact-wrap">
      <header>
        <strong>Got a project or partnership in mind?</strong>
      </header>
      <div>
        <form
          className="form"
          action="https://formspree.io/mukhammadjon.tajiboev@gmail.com"
          method="POST"
        >
          <fieldset className="fieldset">
            <legend className="has-padding-vertical-10">
              Let's get in touch!
            </legend>
            <div className="flex contact-back-details">
              <div className="field">
                <label>Name (required)</label>
                <br />
                <input
                  className="input"
                  type="text"
                  name="nameInput"
                  placeholder="e.g. John Doe"
                  required
                />
              </div>
              <div className="spacer" />
              <div className="field">
                <label>Email (required)</label>
                <input
                  className="input"
                  type="email"
                  name="emailInput"
                  placeholder="e.g. johndoe@gmail.com"
                  required
                />
              </div>
            </div>

            <div className="margin">
              <label>Message</label>
              <textarea
                className="input textarea"
                rows="10"
                name="messageTextarea"
                placeholder="Message..."
                required
              />
            </div>
          </fieldset>
          <button
            className="button button-primary"
            type="submit"
            value="Submit"
          >
            Send message
          </button>
          <p className="alternatively">
            Alternatively, you can send an email to{' '}
            <a href="mailto:mukhammadjon.tajiboev@gmail.com">
              <strong>mukhammadjon.tajiboev@gmail.com</strong>
            </a>
            <br />
            or contact me on social media{' '}
            <a href="https://linktr.ee/mukhammadjon">
              <strong>(Links are here!)</strong>
            </a>
          </p>
        </form>
      </div>
    </div>
  </section>
)

export default form
