import React from 'react'
import './offcanvas.sass'

const offcanvas = props => {
  let asideClasses = 'offcanvas'
  if (props.show) {
    asideClasses = 'offcanvas open'
  }
  return (
    <aside className={asideClasses}>
      <ul className="offcanvas-ul">
        <li className="offcanvas-ul-li">
          <a href="#experience" onClick={props.linkClick}>
            Experience
          </a>
        </li>
        <li className="offcanvas-ul-li">
          <a href="#technologies" onClick={props.linkClick}>
            Technologies
          </a>
        </li>
        <li className="offcanvas-ul-li">
          <a href="#contact" onClick={props.linkClick}>
            Contact
          </a>
        </li>
      </ul>
    </aside>
  )
}

export default offcanvas
